import { Employee } from "generated/prisma";
import { CreateEmployeeRoleDto } from "../types/dtos/employee-role.dto";
import { ExpandedEmployee } from "../types/prisma.types";

export type EmployeeRoleForm = {
  name: string;
  description: string;
  employees: (Employee | ExpandedEmployee)[];
};

export const getDefaultEmployeeRoleForm = (): EmployeeRoleForm => ({
  name: '',
  description: '',
  employees: [] as (Employee | ExpandedEmployee)[]
});

export const convertEmployeeRoleFormToDto = (form: EmployeeRoleForm): CreateEmployeeRoleDto => ({
  name: form.name.trim(),
  description: form.description.trim() || null,
  employeeIds: form.employees.map(e => e.id)
});